import React from 'react';
import {Container} from "react-bootstrap";

function AboutUs(props) {
    return (
        <Container id={"about-us"} className={'mt-5 '}>
            <h2 className={"text-start"}>3.  </h2>
            <hr className={'mt-0 mb-4'}></hr>
            <div className={"white-text-block"} style={{backgroundImage: 'url("background.jpg")'}}>
                <h5 className={"text-start mb-0"}> :</h5>
                <div className={"text-start"}>
                    :      .        ,       .     (Pb, Cd, Zn),        ,   .

                </div>
                <h5 className={"mt-0 text-start mb-0"}>:</h5>
                <p className={"text-start"}>
                    1)       ,        . 2)           ,       .
                </p>
            </div>
        </Container>
    );
}

export default AboutUs;